<template>
  <div>
    <div class="shopcart">
      <!--内容-->
      <div class="content" @click="toggleList">
        <!--左内容-->
        <div class="content-left">
          <!--logo-->
          <div class="logo-wrapper">
            <div class="logo" :class="{'highLight':totalCount>0}">
              <i class="icon-shopping_cart" :class="{'highLight':totalCount>0}"></i>
            </div>
            <div class="num" v-show="totalCount>0">
              {{totalCount}}
            </div>
          </div>
          <!--价格描叙-->
          <div class="price" :class="{'highLight':totalCount>0}" >￥{{totalPrice}}</div>
          <div class="desc">另需配送费￥{{deliveryPrice}}元</div>

        </div>
        <!--右内容-->
        <div class="content-right" @click.stop.prevent="pay">
          <div class="pay" :class="payClass">
           {{payDesc}}
          </div>
        </div>

      </div>
      <!--小球-->
      <div class="ball-container"  v-for="(ball,index) in balls" >

          <transition
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter"
            name="drop">
            <div v-show="ball.show" class="ball"   >
              <div class="inner inner-hook" ></div>
            </div>

          </transition>



      </div>
      <!--购物车详情-->
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li class="food" v-for="food in selectFoods">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>￥{{food.price*food.count}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>

              </li>
            </ul>
          </div>
        </div>
      </transition>

    </div>

  <!--购物车列表背景模糊实现-->
  <transition>
    <div class="list-mask" v-show="listShow" @click="hideList"></div>
  </transition>
  </div>

</template>

<script>
  import cartcontrol from '../cartcontrol/cartcontrol'
  import BScroll from 'better-scroll'
    export default {
        //name: "shopcart",
      data(){
          return {
            balls:[
              {show : false},
              {show : false},
              {show : false},
              {show : false},
              {show : false}
            ],
            dropBalls:[],
            //购物车详情状态
            fold:true
          }
      },
      props:{
          deliveryPrice:{
            default:0
          },
        minPrice:{
          default:0
        },
        selectFoods:{
            default(){
              return[
                {price:10,count:1}
              ]
            }
        }
      },
      computed:{
          //商品总价
          totalPrice(){
            let total =0
            this.selectFoods.forEach((food)=>{
              total+=food.price*food.count

            })
            return total
          },
        //商品总和
        totalCount(){
            let count = 0;
            this.selectFoods.forEach((food)=>{
              count +=food.count
            })
          return count
        },
        //右侧描叙
        payDesc(){
            if(this.totalPrice===0){
              return `￥${this.minPrice}起送`
            }else if(this.totalPrice<this.minPrice){
              let diff = this.minPrice - this.totalPrice
              return `还差￥${diff}起送`
            }else {
              return '结算'
            }
        },
        payClass(){
            if(this.totalPrice<this.minPrice){
              return 'not-enough'
            }else {
              return 'enough'
            }
        },
        listShow(){
            if(!this.totalCount){
              this.folod=true
              return false
            }
            let show =!this.fold
          if(show){
              this.$nextTick(()=>{
                if(!this.scroll){
                  this.scroll =new BScroll(this.$refs.listContent,{
                    click: true
                  })
                }else{
                  this.scroll.refresh()
                }

              })
          }
            return show
        }
      },
      methods: {
        drop(el) {
          for (let i = 0; i < this.balls.length; i++) {
            let ball = this.balls[i];
            if (!ball.show) {
              ball.show = true;
              ball.el = el;
              this.dropBalls.push(ball);
              return;
            }
          }
        },
        beforeEnter: function (el) {
          let count = this.balls.length;
          while (count--) {
            let ball = this.balls[count];
            if (ball.show) {
              /*获取加号的位置(小球动画开始的位置)*/
              let rect = ball.el.getBoundingClientRect();
              let x = rect.left - 32;
              let y = -(window.innerHeight - rect.top-35);
              /*注释部分为动画为一层时代码*/
              // el.style.display = '';
              // el.style.webkitTransform=`translate3d(${x}px,${y}px,0)`;
              // el.style.transform = `translate3d(${x}px, ${y}px, 0)`;

              el.style.display = '';
               el.style.webkitTransform = `translate3d(0, ${y}px, 0)`;
              el.style.transform = `translate3d(0, ${y}px, 0)`;
              /*内层动画(这个动画在这里被分成两层，也可以一层)*/
              let inner = el.getElementsByClassName('inner-hook')[0];
              inner.style.webkitTransform = `translate3d(${x}px, 0, 0)`;
              inner.style.transform = `translate3d(${x}px, 0, 0)`;
            }
          }
        },
        enter: function (el, done) {
          /* eslint-disable no-unused-vars */
          let rf = el.offsetHeight;                 /*触发浏览器重绘;*/
          this.$nextTick(() => {
            el.style.webkitTransform = 'translate3d(0, 0, 0)';
            el.style.transform = 'translate3d(0, 0, 0)';
            let inner = el.getElementsByClassName('inner-hook')[0];
            inner.style.webkitTransform = 'translate3d(0, 0, 0)';
            inner.style.transform = 'translate3d(0, 0, 0)';
           el.addEventListener('transitionend', done);
          });
        },
        afterEnter: function (el) {
          let ball = this.dropBalls.shift();
          if (ball) {
            ball.show = false;
            el.style.display = 'none';
          }
        },
        //购物车详情控制
        toggleList(){
          if(!this.totalCount){
            return
          }
          this.fold=!this.fold
        },
        //购物车清空
        empty(){
          this.selectFoods.forEach((food)=>{
            food.count = 0
          })
        },
        //点击购物车详情背景关闭
        hideList(){
          this.fold=true
        },
        //购物车结算
        pay(){
          if(this.totalPrice<this.minPrice){
            return;
          }
          window.alert(`支付${this.totalPrice}元`)
        }
      },
      components:{cartcontrol}

      //购物车详情开关



      }


</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"


  .shopcart
    position fixed
    left 0
    bottom 0
    z-index 50
    width 100%
    height 48px
    background #000
    .content
      display flex
      background #141d27
      font-size 0
      color rgba(255,255,255,0.4)
      .content-left
        flex 1
        .logo-wrapper
          display inline-block
          vertical-align top
          position relative
          top -10px
          margin 0 12px
          padding 6px
          width 56px
          height 56px
          box-sizing border-box
          border-radius 50%
          background #141d27
          .logo
            width 100%
            height 100%
            border-radius 50%
            text-align center
            background #2b343c
            &.highLight
              background rgb(0,160,220)
            .icon-shopping_cart
              line-height 44px
              font-size 24px
              color #80858a
              &.highLight
                color #fff

          .num
            position absolute
            top 0
            right 0
            width 24px
            height 16px
            line-height 16px
            text-align center
            border-radius 16px
            font-size 9px
            font-weight 700
            color #fff
            background rgb(240,20,20)
            box-shadow 0 4px 8px 0 rgba(0,0,0,0.4)
        .price
          display inline-block
          vertical-align top
          margin-top 12px
          line-height 24px
          padding-right 12px
          box-sizing border-box
          border-right 1px solid rgba(255,255,255,0.1)
          font-size 16px
          font-weight 700
          color rgba(255,255,255,0.4)
          &.highLight
            color #fff
        .desc
          display inline-block
          line-height 24px
          margin 12px 0 0 12px
          color rgba(255,255,255,0.4)
          font-size 10px


      .content-right
        flex 0 0 105px
        width 105px
        .pay
          height 48px
          line-height 48px
          text-align center
          font-size 12px
          font-weight 700
          background #2b333b
          &.not-enough
            background #2b333b
          &.enough
            background #00b43c
            color #fff




    .ball-container
      .ball
        position fixed
        left 32px
        bottom 22px
        z-index 200
        transition all 04s cubic-bezier(0.49, -0.29, 0.75, 0.41)
        .inner
          width 16px
          height 16px
          border-radius 50%
          background rgb(0,160,220)
          transition all 0.4s linear



    .shopcart-list
      position absolute
      left 0
      top 0
      z-index -1
      width 100%
      transition all 0.5s
      transform translate3d(0,-100%,0)
      &.fold-enter-active,&.fold-leave-active
        transform translate3d(0,0,0)
      .list-header
        height 40px
        line-height 40px
        padding 0 18px
        background #f3f5f7
        border-bottom 1px solid rgba(1,17,27,0.1)
        .title
          float left
          font-size 14px
          color rgb(7,17,27)
        .empty
          float right
          font-size 12px
          color rgb(0,160,220)


      .list-content
        padding 0 18px
        max-height 217px
        overflow hidden
        background #fff
        .food
          position  relative
          padding 12px 0
          box-sizing border-box
          border-1px(rgba(1,17,27,0.1))
          .name
            line-height 24px
            font-size 14px
            color rgb(7,17,27)
          .price
            position absolute
            right 90px
            bottom 12px
            line-height 24px
            font-size 14px
            font-weight 700
            color rgb(240,20,20)
          .cartcontrol-wrapper
            position absolute
            right 0
            bottom 6px





  .list-mask
    position fixed
    top 0
    left 0
    width 100%
    height 100%
    z-index 40
    -webkit-backdrop-filter blur(10px)
    opacity 1
    background rgba(7,17,27,0.6)
    &.fade-enter-active,&.fade-leave-active
      opacity 0
      background rgba(7,17,27,0.6)










</style>



